// const WebSocket = require('ws');


let globalwsurl ="";
let globalovertime =5000;
let globalhartbeettime =1000;
let globallastHeartBeat =1000;
let wait_init =true;
let ws ="";
let globalid =1;
let task_callfun={};//任务回调缓存池
let globaltimeout=8000;
let globalbaseurl = "";

function onopen (evt){
    console.log("websocket连接成功",evt.data)
}

function onclose(evt){
    console.log("连接关闭",evt.data)
}
function onerror(evt){
    console.log("连接错误",evt.data)
}



function onmessage(evt){
    globallastHeartBeat = new Date().getTime();
    try {
        let data = JSON.parse(evt.data);  
        // 解析是否带有Id 
        if (data["id"]){
            let call_back=task_callfun[data["id"]];
            if (call_back){
                call_back({result:true,data:data});
            }
            delete task_callfun[data["id"]];
        }
    } catch(e) {
        // empty
    }
}
// 判断是否已经有一个连接
function hasconnect(){
    if ((new Date().getTime() - globallastHeartBeat) > globalovertime) {
        console.log(new Date().getTime() ,globallastHeartBeat,globalovertime)
        return false
    }else{
        return true 
    }
}

function init(wsurl){
    if (hasconnect()){
        return
    }

    globalwsurl=wsurl;
    globallastHeartBeat=new Date().getTime();
    ws = new WebSocket(globalwsurl);
    ws.onopen = onopen;
    ws.onclose = onclose;
    ws.onmessage = onmessage;
    ws.onerror = onerror;

    if (wait_init){
        wait_init=false;
        setInterval(checkConnect,globalovertime);
        setInterval(ping,globalhartbeettime);
    }
}

function checkConnect() {
    if ((new Date().getTime() - globallastHeartBeat) > globalovertime) {
        console.log(new Date().getTime() ,globallastHeartBeat,globalovertime,"重连中...")
        reConnect();
    } 
}

function reConnect(){
    ws.close();
    console.log(globalwsurl,"连接断开，正在尝试重新建立连接...");
    init(globalwsurl);
}
function ping(){
    if (ws.readyState==1){
        ws.send("PING");
    }
}
// 发送一条消息
function sendmsg(payload){
    try{
        let msg =JSON.stringify(payload);
        ws.send(msg);
    }catch(e){
        // empty
    }
}
// 执行任务
function dotask(payload,call_back){
    payload["id"]=globalid;
    globalid+=1;
    sendmsg(payload);
    task_callfun[payload["id"]]=call_back;
    setTimeout(function(){ 
        // 使鼠标粘贴和剪切时，输入框内内容为最新
        let call_back=task_callfun[payload["id"]];
        if (call_back){
            call_back({result:false,data:"超时未执行"});
        }
        delete task_callfun[payload["id"]];
    }, globaltimeout);
}

function http_get(url,params,header){
    if (!params){
        params={};
    }
    if (!header){
        header={};
    }
    let payload = {
        action:"proxy",
        method:"GET",
        params:params,
        requrl:url,
        header:header,
    }
    return new Promise((resolve) => {
        dotask(payload,function(ret){
            resolve(ret)
        })
      }).then((ret) => {
          if (ret.result){
              if (ret.data.code==200){
                  try{
                    return JSON.parse(ret.data.data);
                  }catch(e){
                    return {code:1001,error:ret.data.data,msg:ret.data.msg,data:ret.data.data} 
                  }
                  
              }else{
                return {code:ret.data.code,error:ret.data.error,msg:ret.data.msg} 
              }

          }else{
            return {code:1001,error:ret.data} 
          }
      })
}
