// 执行一条任务
exports.DoTask=dotask;
// 任务超时时间
exports.SetTimeout=function(timeout){
    globaltimeout=timeout;
};
// 发送一条消息
exports.SendMsg=sendmsg;

// 设置baseUrl 
exports.SetBaseUrl =function(baseurl){
    globalbaseurl=baseurl;
}

// 设置baseUrl 
exports.InitConnect =init;

exports.get=http_get;
exports.post=http_post;
exports.put=http_put;
exports.delete=http_delete;

exports.path_get=(path,params,header)=>{
    return http_get(globalbaseurl+path,params,header);
};
exports.path_post=(path,params,header)=>{
    return http_post(globalbaseurl+path,params,header);
};
exports.path_put=(path,params,header)=>{
    return http_put(globalbaseurl+path,params,header);
};
exports.path_delete=(path,params,header)=>{
    return http_delete(globalbaseurl+path,params,header);
};

